import React from 'react'
import './SignInForm.css'

class SignInForm extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            login: "",
            password: ""
        }
    }

    render() {
        return(
            <form className="signList">
                <h2>Вход</h2>
                <input className="textInput" type="text" placeholder="    Login *" required></input>
                <input className="textInput" type="password" placeholder="    Пароль *" required></input>
                <button><p>Войти</p></button>
            </form>
        );
    }
}

export default SignInForm;