import React from 'react'
import './RegForm.css'

class RegForm extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            name: "",
            surname: "",
            email: "",
            password: "",
            image: undefined
        }

        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event){
        const target = event.target;
        const name = target.name;
        const value = target.value;
        this.setState({
            [name] : value
        });
    }

    onSubmit = event => {
        const {email, password} = this.state;

        this.props.firbase.registrUser(email, password).then(authUser => {
            this.setState({
                name: "",
                surname: "",
                email: "",
                password: "",
                image: undefined
            })
        })
        event.preventDefault();
        alert("Вы успешно зарегистрированы!");
    }

    render() {
        const {name, surname, email, password, image} = this.state;
        return(
            <form className="regList" onSubmit={this.onSubmit}>
                <h2>Авторизация</h2>
                <input className="textInput" name="name" value={name} onChange={this.handleChange} type="text" placeholder="    Имя" pattern="[А-Яа-яЁё]+"></input>
                <input className="textInput" name="surname" value={surname} onChange={this.handleChange} type="text" placeholder="    Фамилия" pattern="[А-Яа-яЁё]+"></input>
                <input className="textInput" name="email" value={email} onChange={this.handleChange} type="text" placeholder="    Login *" required></input>
                <input className="textInput" name="password" value={password} onChange={this.handleChange} type="text" placeholder="    Пароль *" required></input>
                <input type="file" name="image" value={image} onChange={this.handleChange} accept="image/*" className="imageBut"></input>
                <button><p>Отправить</p></button>
            </form>
        );
    }
}

export default RegForm;